import React, { useState, useEffect } from "react";
import { Modal, Button } from "antd";
import { useDispatch, useSelector } from "react-redux";
const Schedule = ({ sheduleModal, onClose, id }) => {
  const [isModalVisible, setIsModalVisible] = useState(false);

  useEffect(() => {
    if (sheduleModal) {
      setIsModalVisible(true);
    }
  }, [sheduleModal]);

  const handleOk = () => {
    setIsModalVisible(false);
    onClose();
  };
  const handleCancel = () => {
    setIsModalVisible(false);
    onClose();
  };

  // find contractors

  const [contractor, setContractor] = useState(null);
  // Fetching
  const { contractors } = useSelector((state) => state.contractorFetch);

  useEffect(() => {
    if (contractors) {
      const contractorById = Array.from(contractors).find((x) =>
        String(x._id === String(id))
      );
      console.log(contractors);
      console.log(contractorById);
    }
  }, [contractors, id]);

  return (
    <>
      <Modal
        title="Customize Schedule"
        visible={isModalVisible}
        onOk={handleOk}
        onCancel={handleCancel}
      >
        <p>Some contents...</p>
        <p>Some contents...</p>
        <p>Some contents...</p>
      </Modal>
    </>
  );
};

export default Schedule;
